import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ControlExportGoodsComponent } from './control-export-goods.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { FormsModule } from '@angular/forms';
import { DetailOrderComponent } from './detail-order/detail.component';

const routes: Routes = [
  {
    path: "",
    component: ControlExportGoodsComponent
  },
  {
    path: 'detail-order',
    component: DetailOrderComponent
  },
]

@NgModule({
  declarations: [ControlExportGoodsComponent, DetailOrderComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class ControlExportGoodsModule { }
